import { lazy } from 'react';

const CookiesGacha = lazy(() => import('./pages/cookies/CookiesGacha'));
const CookiesInfo = lazy(() => import('./pages/cookies/CookiesInfo'));
const ArtifactsGacha = lazy(() => import('./pages/artifacts/ArtifactsGacha'));
const ArtifactsInfo = lazy(() => import('./pages/artifacts/ArtifactsInfo'));
const EquipmentGacha = lazy(() => import('./pages/equipments/EquipmentGacha'));
const EquipmentInfo = lazy(() => import('./pages/equipments/EquipmentInfo'));
const GuidePage = lazy(() => import('./pages/guides/GuidePage'));
const MainContent = lazy(() => import('./components/MainContent'));
const CookieDetailPage = lazy(() => import('./pages/cookies/CookieDetailPage'));
const GlassInfo = lazy(() => import('./pages/guides/glass/GlassInfo'));
const WorldmapInfo = lazy(() => import('./pages/guides/worldmap/WorldmapInfo'));
const MiningInfo = lazy(() => import('./pages/guides/mining/MiningInfo'));
const HexagonalBeeInfo = lazy(() => import('./pages/guides/hexagonalbee/HexagonalBeeInfo'));

const pages = [
    { path: '/cookies/gacha', component: CookiesGacha },
    { path: '/cookies/info', component: CookiesInfo },
    { path: '/artifacts/gacha', component: ArtifactsGacha },
    { path: '/artifacts/info', component: ArtifactsInfo },
    { path: '/equipments/gacha', component: EquipmentGacha },
    { path: '/equipments/info', component: EquipmentInfo },
    { path: '/guides/main', component: GuidePage },
    { path: '/', component: MainContent },
    { path: '/cookies/:cookieName', component: CookieDetailPage },
    { path: '/guides/hexagonalbee/main', component: HexagonalBeeInfo },
    { path: '/guides/worldmap/main', component: WorldmapInfo },
    { path: '/guides/mining/main', component: MiningInfo },
    { path: '/guides/glass/main', component: GlassInfo },
];

export default pages;
