import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { CSSTransition } from 'react-transition-group';
import './Header.css'; 
import StyledLink from './StyledLink';

const HeaderContainer = styled.header`
  position: fixed;
  top: 0;
  width: 100%;
  max-width: 480px;
  background-color: #ffffff;
  z-index: 1000;
`;

const HeaderContents = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
`;

const Logo = styled.img`
  cursor: pointer;
  height: 20px;
  padding: 10px 0px 0px 0px;
`;

const MenuToggle = styled.div`
  cursor: pointer;
  font-size: 24px;
`;

const OptionsList = styled.ul`
  position: absolute;
  top: 50px;
  left: 0;
  width: 100%;
  background-color: #ffffff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  list-style: none;
  padding: 0;
  margin: 0;
  overflow: hidden;
  border-radius: 0px 0px 10px 10px;
`;

const OptionItem = styled.li`
  padding: 10px 20px;
  cursor: pointer;
  border-bottom: 1px solid #f0f0f0;
  font-size: 15px;
  position: relative;  /* 변경된 스타일 */
  transition: background-color 0.3s, width 0.3s, height 0.3s; /* 부드러운 전환 추가 */
  
  &:hover {
    background-color: #f0f0f0;
    border-radius: 10px;
    width: 90%; /* 요소 가로 크기를 98%로 늘리기 */
    height: auto; /* 높이 변경 */
    margin: 0 auto; /* 가운데 정렬 */
  }

  &.expanded::after {  /* 배경색을 변경할 때 사용 */
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #e9e9e9;
    z-index: -1;
  }
`;

const SubOptionsList = styled.ul`
  list-style: none;
  padding: 0;
  margin: 10px 0 0 0; /* 옵션 아이템과 서브 옵션 아이템 사이 간격 조정 */
  background-color: #e9e9e9;
  overflow: hidden;
  border-radius: 10px;
  transition: max-height 0.3s ease;
`;

const SubOptionItem = styled.li`
  padding: 10px 40px;
  margin: 5px;
  cursor: pointer;
  background-color: #e9e9e9;
  border-radius: 10px;
  text-decoration: none;
  

  &:hover {
    background-color: #e0e0e0;
    border-radius: 10px;
    text-decoration: none;
  }
`;

const Header = () => {
  const [showOptions, setShowOptions] = useState(false);
  const [subMenu, setSubMenu] = useState(null);

  const toggleOptions = () => {
    setShowOptions(!showOptions);
    setSubMenu(null);
  };

  const handleSubMenu = (menu) => {
    setSubMenu(subMenu === menu ? null : menu);
  };

  const handleCloseMenu = () => {
    setShowOptions(false);
    setSubMenu(null);
  };

  useEffect(() => {
    if (showOptions) {
      document.body.addEventListener('click', handleCloseMenu);
    } else {
      document.body.removeEventListener('click', handleCloseMenu);
    }

    return () => {
      document.body.removeEventListener('click', handleCloseMenu);
    };
  }, [showOptions]);

  return (
    <HeaderContainer>
      <HeaderContents>
        <StyledLink to="/">
          <Logo src="https://sseong-mi.github.io/Tower-of-Adventures/images/items/realcookie.png" alt="Logo" />
        </StyledLink>
        <MenuToggle onClick={(e) => {
          e.stopPropagation();
          toggleOptions();
        }}>
          ☰
        </MenuToggle>
      </HeaderContents>
      <CSSTransition
        in={showOptions}
        timeout={300}
        classNames="options"
        unmountOnExit
      >
        <OptionsList onClick={(e) => e.stopPropagation()}>
          <OptionItem onClick={() => handleSubMenu('cookies')} className={subMenu === 'cookies' ? 'expanded' : ''}>
            쿠키
            <CSSTransition
              in={subMenu === 'cookies'}
              timeout={300}
              classNames="sub-options"
              unmountOnExit
            >
              <SubOptionsList>
                <StyledLink to="/cookies/gacha" onClick={handleCloseMenu}><SubOptionItem>쿠키 뽑기</SubOptionItem></StyledLink>
                <StyledLink to="/cookies/info" onClick={handleCloseMenu}><SubOptionItem>쿠키 정보</SubOptionItem></StyledLink>
              </SubOptionsList>
            </CSSTransition>
          </OptionItem>
          <OptionItem onClick={() => handleSubMenu('equipment')} className={subMenu === 'equipment' ? 'expanded' : ''}>
            장비
            <CSSTransition
              in={subMenu === 'equipment'}
              timeout={300}
              classNames="sub-options"
              unmountOnExit
            >
              <SubOptionsList>
                <StyledLink to="/equipments/gacha" onClick={handleCloseMenu}><SubOptionItem>장비 뽑기</SubOptionItem></StyledLink>
                <StyledLink to="/equipments/info" onClick={handleCloseMenu}><SubOptionItem>장비 정보</SubOptionItem></StyledLink>
              </SubOptionsList>
            </CSSTransition>
          </OptionItem>
          <OptionItem onClick={() => handleSubMenu('artifacts')} className={subMenu === 'artifacts' ? 'expanded' : ''}>
            아티펙트
            <CSSTransition
              in={subMenu === 'artifacts'}
              timeout={300}
              classNames="sub-options"
              unmountOnExit
            >
              <SubOptionsList>
                <StyledLink to="/artifacts/gacha" onClick={handleCloseMenu}><SubOptionItem>아티펙트 뽑기</SubOptionItem></StyledLink>
                <StyledLink to="/artifacts/info" onClick={handleCloseMenu}><SubOptionItem>아티펙트 정보</SubOptionItem></StyledLink>
              </SubOptionsList>
            </CSSTransition>
          </OptionItem>
          <OptionItem onClick={() => handleSubMenu('guides')} className={subMenu === 'guides' ? 'expanded' : ''}>
            공략
            <CSSTransition
              in={subMenu === 'guides'}
              timeout={300}
              classNames="sub-options"
              unmountOnExit
            >
              <SubOptionsList>
                <StyledLink to='/guides/worldmap/main' onClick={handleCloseMenu}><SubOptionItem>월드맵 공략</SubOptionItem></StyledLink>
                <StyledLink to='/guides/mining/main' onClick={handleCloseMenu}><SubOptionItem>돌파석 채광장 공략</SubOptionItem></StyledLink>
                <StyledLink to='/guides/hexagonalbee/main' onClick={handleCloseMenu}><SubOptionItem>육각벌의 숲 공략</SubOptionItem></StyledLink>
                <StyledLink to='/guides/glass/main' onClick={handleCloseMenu}><SubOptionItem>유리미궁 공략</SubOptionItem></StyledLink>
              </SubOptionsList>
            </CSSTransition>
          </OptionItem>
        </OptionsList>
      </CSSTransition>
    </HeaderContainer>
  );
};

export default Header;
